import React from 'react';
import './App.css';
import StatCard from './StatCard';
import MatchSetCard from './MatchSetCard';
import StatPercentageCard from './StatPercantageCard';

let items=['Item 1','Item 2','Item 3'];
let columns=["", "", "", ""];
let titles=["Aces", "Blocks", "Kill", "Missed Serves", "Blocking Error", "Hitting Error", "Aces Against", "Blocking Touch", "Free Balls", "5 Star Volleyball", "Match/Set", "Opponent"]

interface IProps {

}

interface IState {
  pointsEarned: {
    Aces: number,
    Blocks: number,
    Kills: number
  },
  pointsLost: {
    MissedServes: number,
    BlockingError: number,
    AcesAgainst: number,
    HittingError: number,
  },
  game: {
    Match: number,
    Set: number,
    FiveStarScore: number,
    OpponentScore: number
  },
  extra: {
    BlockingTouch: number,
    FreeBalls: number
  }
}

export default class App extends React.Component<IProps,IState> {
  constructor(props: IProps) {
    super(props);
    this.state = {
      pointsEarned: {
        Aces: 0,
        Blocks: 0,
        Kills: 0
      },
      pointsLost: {
        MissedServes: 0,
        BlockingError: 0,
        AcesAgainst: 0,
        HittingError: 0
      },
      game: {
        Match: 0,
        Set: 0,
        FiveStarScore: 0,
        OpponentScore: 0
      },
      extra: {
        BlockingTouch: 0,
        FreeBalls: 0
      }
    }
    this.setAces = this.setAces.bind(this);
    this.setBlocks = this.setBlocks.bind(this);
    this.setKills = this.setKills.bind(this);
    
    this.setMissedServes = this.setMissedServes.bind(this);
    this.setBlockingError = this.setBlockingError.bind(this);
    this.setHittingError = this.setHittingError.bind(this);
    this.setAcesAgainst = this.setAcesAgainst.bind(this);

    this.setBlockingTouch = this.setBlockingTouch.bind(this);
    this.setFreeBalls = this.setFreeBalls.bind(this);

    this.setFiveStarScore = this.setFiveStarScore.bind(this);
    this.setOpponentScore = this.setOpponentScore.bind(this);
  }

  setAces(count: number) : void {
    this.setState({pointsEarned: Object.assign(this.state.pointsEarned, {Aces: count})});
  }

  setBlocks(count: number) : void {
    this.setState({pointsEarned: Object.assign(this.state.pointsEarned, {Blocks: count})});
  }

  setKills(count: number) : void {
    this.setState({pointsEarned: Object.assign(this.state.pointsEarned, {Kills: count})});
  }

  setMissedServes(count: number) : void {
    this.setState({pointsLost: Object.assign(this.state.pointsLost, {MissedServes: count})});
  }

  setBlockingError(count: number) : void {
    this.setState({pointsLost: Object.assign(this.state.pointsLost, {BlockingError: count})});
  }

  setHittingError(count: number) : void {
    this.setState({pointsLost: Object.assign(this.state.pointsLost, {HittingError: count})});
  }

  setAcesAgainst(count: number) : void {
    this.setState({pointsLost: Object.assign(this.state.pointsLost, {AcesAgainst: count})});
  }

  setBlockingTouch(count: number) : void {
    this.setState({extra: Object.assign(this.state.extra, {BlockingTouch: count})});
  }

  setFreeBalls(count: number) : void {
    this.setState({extra: Object.assign(this.state.extra, {FreeBalls: count})});
  }

  setFiveStarScore(count: number) : void {
    this.setState({game: Object.assign(this.state.game, {FiveStarScore: count})});
  }

  setOpponentScore(count: number) : void {
    this.setState({game: Object.assign(this.state.game, {OpponentScore: count})});
  }

  clear() : void {
    this.setState({
      pointsEarned: {
        Aces: 0,
        Blocks: 0,
        Kills: 0
      },
      pointsLost: {
        MissedServes: 0,
        BlockingError: 0,
        AcesAgainst: 0,
        HittingError: 0
      },
      game: {
        Match: this.state.game.Match,
        Set: this.state.game.Set,
        FiveStarScore: 0,
        OpponentScore: 0
      },
      extra: {
        BlockingTouch: 0,
        FreeBalls: 0
      }
    })
  }

  render() {
    let count = 0;
    return (
      <div className="container">
        <div className="board">
          {
            columns.map((u, i) => {
              let marginTop = 0;
              let marginBottom = 0;
              if (i === 0) { 
                marginBottom = 5;
              }
              if (i === 1) {
                marginTop = 5;
                marginBottom = 5;
              }
              if (i === 2) {
                marginTop = 5;
              }
              return (
                // ROW
                <div key={i} style={{marginTop: marginTop, marginBottom: marginBottom, width: '100%', height: '24%', display: "flex", justifyContent: "space-evenly", flexDirection: "row", textAlign: "center"}}> 
                  {
                    items.map((value, index) => {
                      let marginLeft = 0;
                      let marginRight = 0;
                      if (index === 1) { 
                        marginLeft = 10;
                        marginRight = 10;
                      }
                      let key = titles[count];
                      count++;
                      switch(count - 1) {
                        case 0:
                          return(
                            <StatCard onChange={this.setAces} count={this.state.pointsEarned.Aces} marginLeft={marginLeft} marginRight={marginRight} key={key} title={key}></StatCard>
                          )
                        case 1:
                          return(
                            <StatCard onChange={this.setBlocks} count={this.state.pointsEarned.Blocks} marginLeft={marginLeft} marginRight={marginRight} key={key} title={key}></StatCard>
                          )
                        case 2:
                          return(
                            <StatCard onChange={this.setKills} count={this.state.pointsEarned.Kills} marginLeft={marginLeft} marginRight={marginRight} key={key} title={key}></StatCard>
                          )
                        case 3:
                          return(
                            <StatCard onChange={this.setMissedServes} count={this.state.pointsLost.MissedServes} marginLeft={marginLeft} marginRight={marginRight} key={key} title={key}></StatCard>
                          )
                        case 4:
                          return(
                            <StatCard onChange={this.setBlockingError} count={this.state.pointsLost.BlockingError} marginLeft={marginLeft} marginRight={marginRight} key={key} title={key}></StatCard>
                          )
                        case 5:
                          return(
                            <StatCard onChange={this.setHittingError} count={this.state.pointsLost.HittingError} marginLeft={marginLeft} marginRight={marginRight} key={key} title={key}></StatCard>
                          )
                        case 6:
                          return(
                            <StatCard onChange={this.setAcesAgainst} count={this.state.pointsLost.AcesAgainst} marginLeft={marginLeft} marginRight={marginRight} key={key} title={key}></StatCard>
                          )
                        case 7:
                          return(
                            <StatCard onChange={this.setBlockingTouch} count={this.state.extra.BlockingTouch} marginLeft={marginLeft} marginRight={marginRight} key={key} title={key}></StatCard>
                          )
                        case 8:
                          return(
                            <StatCard onChange={this.setFreeBalls} count={this.state.extra.FreeBalls} marginLeft={marginLeft} marginRight={marginRight} key={key} title={key}></StatCard>
                          )
                        case 9:
                          return(
                            <StatCard onChange={this.setFiveStarScore} count={this.state.game.FiveStarScore} marginLeft={marginLeft} marginRight={marginRight} key={key} title={key}></StatCard>
                          )
                        case 10:
                          return(
                            <MatchSetCard marginLeft={marginLeft} marginRight={marginRight} uid={key}></MatchSetCard>
                          )
                        case 11:
                          return(
                            <StatCard onChange={this.setOpponentScore} count={this.state.game.OpponentScore} marginLeft={marginLeft} marginRight={marginRight} key={key} title={key}></StatCard>
                          )
                          default: 
                          return (
                            <h1>SOMETHING IS WRONG</h1>
                          )
                      }
                    })
                  }
                </div>
              )
            })
          }
          <div onClick={() => {this.clear()}} style={{cursor: "pointer", width: "33.33%", margin: "auto", backgroundColor: "red", borderRadius: 15}}>
            <p style={{color: "white", fontSize: 32, fontWeight: 'bold', margin: 0, fontFamily: "Avenir Next", textAlign: "center"}}>CLEAR</p>
          </div>
        </div>
        <StatPercentageCard currentState={this}></StatPercentageCard>
      </div>
    )
  }
}