import React from 'react';
import App from './App';
import './statpercentagecard.css';

interface IProps {
  currentState: App,
}

interface IState {
  AcePercentage: number,
  BlockPercentage: number,
  KillPercentage: number,
  MSPercantage: number,
  BEPercentage: number,
  HEPercentage: number,
  AAPercentage: number
}

export default class StatPercentageCard extends React.Component<IProps, IState> {
    constructor(props: IProps) {
        super(props);
        let appState = props.currentState.state;
        let fiveStarScore = appState.game.FiveStarScore;
        let opponentScore = appState.game.OpponentScore;
        this.state = {
          AcePercentage: fiveStarScore !== 0 ? (appState.pointsEarned.Aces / fiveStarScore) * 100 : 0,
          BlockPercentage: fiveStarScore !== 0 ? (appState.pointsEarned.Blocks / fiveStarScore) * 100 : 0,
          KillPercentage: fiveStarScore !== 0 ? (appState.pointsEarned.Blocks / fiveStarScore) * 100 : 0,

          MSPercantage: opponentScore !== 0 ? (appState.pointsLost.MissedServes / opponentScore) * 100 : 0,
          BEPercentage: opponentScore !== 0 ? (appState.pointsLost.BlockingError / opponentScore) * 100 : 0,
          HEPercentage: opponentScore !== 0 ? (appState.pointsLost.HittingError / opponentScore) * 100 : 0,
          AAPercentage: opponentScore !== 0 ? (appState.pointsLost.AcesAgainst / opponentScore) * 100 : 0,
        }
    }

    componentWillReceiveProps(props: IProps) : void {
      let appState = props.currentState.state;
      let fiveStarScore = appState.game.FiveStarScore;
      let opponentScore = appState.game.OpponentScore;
      
      this.setState({AcePercentage:  fiveStarScore !== 0 ? Number.parseFloat(((appState.pointsEarned.Aces / fiveStarScore) * 100).toFixed(3)) : 0});
      this.setState({BlockPercentage:  fiveStarScore !== 0 ? Number.parseFloat(((appState.pointsEarned.Blocks / fiveStarScore) * 100).toFixed(3)) : 0});
      this.setState({KillPercentage:  fiveStarScore !== 0 ? Number.parseFloat(((appState.pointsEarned.Kills / fiveStarScore) * 100).toFixed(3)) : 0});

      this.setState({MSPercantage:  opponentScore !== 0 ? Number.parseFloat(((appState.pointsLost.MissedServes / opponentScore) * 100).toFixed(3)) : 0});
      this.setState({BEPercentage:  opponentScore !== 0 ? Number.parseFloat(((appState.pointsLost.BlockingError / opponentScore) * 100).toFixed(3)) : 0});
      this.setState({HEPercentage:  opponentScore !== 0 ? Number.parseFloat(((appState.pointsLost.HittingError / opponentScore) * 100).toFixed(3)) : 0});
      this.setState({AAPercentage:  opponentScore !== 0 ? Number.parseFloat(((appState.pointsLost.AcesAgainst / opponentScore) * 100).toFixed(3)) : 0});
    }


    render() {
        return (
            <div className="stats">
            <div style={{width: '100%', height: '50%', textAlign: "center"}}>
              <div style={{height: "10%", width: "100%"}}>
              <p className="title" style={{fontFamily: "Avenir Next"}}>Points Earned</p>
              </div>
              <div className="pointEarned">
                <div className="half">
                  <p className="textContent" style={{fontFamily: "Avenir Next"}}>Ace %</p>
                </div>
                <div className="half">
                  <p className="textContent" style={{fontFamily: "Avenir Next"}}>{this.state.AcePercentage}</p>
                </div>
              </div>
              <div className="pointEarned">
                <div className="half">
                  <p className="textContent" style={{fontFamily: "Avenir Next"}}>Block %</p>
                </div>
                <div className="half">
                  <p className="textContent" style={{fontFamily: "Avenir Next"}}>{this.state.BlockPercentage}</p>
                </div>
              </div>
              <div className="pointEarned">
                <div className="half">
                  <p className="textContent" style={{fontFamily: "Avenir Next"}}>Kill %</p>
                </div>
                <div className="half">
                  <p className="textContent" style={{fontFamily: "Avenir Next"}}>{this.state.KillPercentage}</p>
                </div>
              </div>
            </div>
            <div style={{width: '100%', height: '50%', textAlign: "center"}}>
              <div style={{height: "10%", width: "100%"}}>
                <p className="title" style={{fontFamily: "Avenir Next"}}>Points Lost</p>
              </div>
              <div className="pointLost">
                <div className="half">
                  <p className="textContent" style={{fontFamily: "Avenir Next"}}>MS %</p>
                </div>
                <div className="half">
                  <p className="textContent" style={{fontFamily: "Avenir Next"}}>{this.state.MSPercantage}</p>
                </div>
              </div>
              <div className="pointLost">
                <div className="half">
                  <p className="textContent" style={{fontFamily: "Avenir Next"}}>BE %</p>
                </div>
                <div className="half">
                  <p className="textContent" style={{fontFamily: "Avenir Next"}}>{this.state.BEPercentage}</p>
                </div>
              </div>
              <div className="pointLost">
                <div className="half">
                  <p className="textContent" style={{fontFamily: "Avenir Next"}}>HE %</p>
                </div>
                <div className="half">
                  <p className="textContent" style={{fontFamily: "Avenir Next"}}>{this.state.HEPercentage}</p>
                </div>
              </div>
              <div className="pointLost">
                <div className="half">
                  <p className="textContent" style={{fontFamily: "Avenir Next"}}>AA %</p>
                </div>
                <div className="half">
                  <p className="textContent" style={{fontFamily: "Avenir Next"}}>{this.state.AAPercentage}</p>
                </div>
              </div>
            </div>
          </div>
        )
    }
}