import React from 'react';

interface IProps {
    uid: string,
    marginLeft: number,
    marginRight: number
}

interface IState {
    match: string,
    set: string,
}

export default class MatchSetCard extends React.Component<IProps, IState> {
    constructor(props: IProps) {
        super(props);
        this.state = {
            match: "0",
            set: "0"
        }
    }

    render() {
        return (
            <div key={this.props.uid} style={{marginLeft: this.props.marginLeft, marginRight: this.props.marginRight, width: "33.33%", height: "90%", background: "#414141", borderRadius: 15, color: "#FFFFFF"}}>
                <div style={{display: "flex", flexDirection: "row", justifyContent: "space-between", height: "25%", textAlign: "center"}}>
                    <div style={{width: "50%"}}>
                        <span style={{fontSize: 28, fontWeight: "bold", fontFamily: "Avenir Next"}}>Match</span>
                    </div>
                    <div style={{width: "50%"}}>
                        <span style={{fontSize: 28, fontWeight: "bold", fontFamily: "Avenir Next"}}>Set</span>
                    </div>
                </div>
                <div style={{display: "flex", height: "75%"}}>
                    <div style={{width: "50%"}}>
                        <input onFocus={() => {this.setState({match: ""})}} onChange={(event) => {this.setState({match: event.target.value})}} value={this.state.match} type={"number"} style={{width: "50%", textAlign: "center", height: "100%", backgroundColor: "transparent", border: "none", fontSize: 28, fontWeight: "bold", fontFamily: "Avenir Next", color: "white"}}></input>
                    </div>
                    <div style={{width: "50%"}}>
                    <input onFocus={() => {this.setState({set: ""})}} onChange={(event) => {this.setState({set: event.target.value})}} value={this.state.set} type={"number"} style={{width: "50%", textAlign: "center", height: "100%", backgroundColor: "transparent", border: "none", fontSize: 28, fontWeight: "bold", fontFamily: "Avenir Next", color: "white"}}></input>
                    </div>
                </div>
            </div>
        )
    }
}