import React from 'react';

interface IProps {
    key: string,
    title: string,
    marginLeft: number,
    marginRight: number,
    count: number, 
    onChange: (count: number) => void
}

interface IState {
    count: number
}

export default class StatCard extends React.Component<IProps, IState> {
    constructor(props: IProps) {
        super(props);
        this.state = {
            count: props.count
        }
    }
    
    componentWillReceiveProps(props: IProps) {
        this.setState({count: props.count});

    }

    subtract() : void {
        this.props.onChange(this.state.count - 1);
        this.setState({count: this.state.count - 1});
    }

    add() : void {
        this.props.onChange(this.state.count + 1);
        this.setState({count: this.state.count + 1});
    }

    render() {
        return (
            <div key={this.props.key} style={{marginLeft: this.props.marginLeft, marginRight: this.props.marginRight, width: "33.33%", height: "90%", background: "#414141", borderRadius: 15, color: "#FFFFFF"}}>
                <div style={{height: "25%", textAlign: "center"}}>
                    <span style={{fontSize: 28, fontWeight: "bold", fontFamily: "Avenir Next"}}>{this.props.title}</span>
                </div>
                <div style={{display: "flex", height: "75%"}}>
                    <div onClick={() => {this.subtract()}} style={{cursor: "pointer", width: "33.33%", height: "100%", display: "flex", justifyContent: "center", textAlign: "center", alignItems: "center", alignContent: "center"}}>
                        <h1>-</h1>
                    </div>
                    <div style={{width: "33.33%", height: "100%", display: "flex", justifyContent: "center", textAlign: "center", alignItems: "center", alignContent: "center"}}>
                        <h1>{this.state.count}</h1>
                    </div>
                    <div onClick={() => {this.add()}} style={{cursor: "pointer", width: "33.33%", height: "100%", display: "flex", justifyContent: "center", textAlign: "center", alignItems: "center", alignContent: "center"}}>
                        <h1>+</h1>
                    </div> 
                </div>
            </div>
        )
    }
}